 .signinForm {
	border-radius: 2px;
	width: 390px;
	height: 500px;
	/* margin: 0 auto;
	padding: 30px 0;
	margin-top: 60px; */
	/* color: black;
	background: #fff; */
	/* border: 1px solid #f3f3f3;
	box-shadow: 0px 2px 2px rgba(252, 250, 250, 0.973); */

	position: absolute;
	transform: translate(-50%,-50%);
	background: rgba(0, 0, 0, 0.5);
	color: #fff;
	box-sizing: border-box;
	padding: 70px 30px;
	top: 50%;
	left: 50%;
} 
.signinForm h2 {
	/* color:black; */
	/* margin: 0 0 15px; */
	text-align: center;
}
.signinForm .lead {
	font-size: 14px;
	/* margin-bottom: 30px; */
	text-align: center;
}

.signinForm .formGroup {
	margin-bottom: 20px;
}
.signinForm label {
	font-weight: normal;
	font-size: 13px;
}

.signinForm input[type="text"],input[type="password"],input[type="email"] {
    border: none;
    border-bottom: 1px solid #fff;
    background: transparent;
    outline: none;
    height: 40px;
	color: #fff;
    font-size: 16px;
}
.signinForm input[type="password"] {
    border: none;
    border-bottom: 1px solid #fff;
    background: transparent;
    outline: none;
    height: 40px;
	color: #fff;
    font-size: 16px;
}
.signinForm input[type="email"] {
    border: none;
    border-bottom: 1px solid #fff;
    background: transparent;
    outline: none;
    height: 40px;
	color: #fff;
    font-size: 16px;
}

.signinForm .but{
    border: none;
    outline: none;
    height: 40px;
    background: #1c8adb;
    color: #fff;
    font-size: 18px;
    border-radius: 20px;
}
.signinForm .but:hover{
    cursor: pointer;
    background: #39dc79;
    color: #000;
}
