.signinForm a {
	color: #19aa8d;
	text-decoration: none;
}	
.signinForm a:hover {
	text-decoration: underline;
}
.signinForm .fa-lock {
	font-size: 21px;
	position: relative;
	top: 8px;
}
.signinForm .fa-user {
	font-size: 21px;
	position: relative;
	top: 8px;
}
.signinForm .fa-paper-plane {
	font-size: 17px;
	margin-right: 4px;
}
.signinForm .fa-check {
	color: #fff; 
	left: 9px;
	top: 18px;
	font-size: 7px;
	position: absolute;
}